import React, { ReactNode } from "react";
import { FaStar } from "react-icons/fa";
import { Kufam } from "next/font/google";
const kufam = Kufam({
  subsets: ["arabic"],
  weight: ["400", "700"], // Add weights as needed
});
interface props {
  titleWord: ReactNode;
}
export default function Title({ titleWord }: props) {
  return (
    <>
      <div className="container flex itmes-center justify-center">
        <div className="before">
          <FaStar /> <FaStar /> <FaStar /> <FaStar /> <FaStar /> <FaStar />
        </div>
        <h1 className={`${kufam.className} text-3xl font-bold underline`}>
          {titleWord}
        </h1>
        <div className="after">
          <FaStar /> <FaStar /> <FaStar /> <FaStar /> <FaStar /> <FaStar />
        </div>
      </div>
    </>
  );
}
